.thumbnail-container{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    align-content: flex-start;
    margin: 0 10%;
}

.thumbnail-work {
    position: relative;
    width: 100%;
    height: auto;
    max-width: 1150px;
}

.play-button  {
    position: absolute;
    transform: rotate(90deg);
    opacity: .8;
}

.work-title {
    display: table;
    margin-right: auto;
    margin-left: auto;
    font-size: 200%;
    font-family: "Lato", sans-serif;
}

@media only screen and (max-width: 700px){
    .work-title{
        font-size: 150%;
    }
}

.work16 {
    position: relative;
    padding-bottom: 45%; /* 16:9 */
    height: 0;
    width: 80%;
    margin: 0 auto;
}

.work16 iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.work235 {
    position: relative;
    padding-bottom: 34%; /* 2.35:1 */
    height: 0;
    width: 80%;
    margin: 0 auto;
}

.work235 iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.stills {
    // display: flex;
    // justify-content: center;
    // align-items: center;
    // flex-wrap: wrap;
    // align-content: flex-start;
    display: grid;
    grid-gap: 5px;
    align-items: stretch;
    justify-items: center;
    column-gap: 1px;
    
}

